// Tama√±os cajas
$total: 100%;
$grande: 85%;
$medio: 50%;
$chico: 25%;


// Map Secciones
$fondoSeccion: (
    fondo: url(../img/fondo/secciones.jpg),
    borde: 30px,
);


// Responsive Mixin
$pc: 1200px;
$tablet: 768px;
$cel: 425px;

@mixin responsive ($medida) {
    @if $medida == pc {
        @media (max-width: $pc){
            @content;}
    }

    @else if $medida == tablet{
        @media (max-width: $tablet){
            @content;}
    }

    @else if $medida == cel{
        @media (max-width: $cel){
            @content;}
    }

    }
